.sidebar{
    top:80px;
    position: sticky;
    flex: 0.2;
    border-radius: 10px;
    text-align: center;
    height: fit-content;
}
.sidebar_avatar{
    margin-bottom: 10px;
}
.sidebar_top{
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px solid lightgray;
    border-bottom: 0;
    border-radius: 10px 10px 0px 0px;
    background-color:white;
    padding-bottom: 10px;
}
.sidebar_top > img{
    margin-bottom: -20px;
    width: 100%;
    height: 60px;
    border-radius: 10px 10px 0 0 ;
    object-fit: cover;
}
.sidebar_top > h2{
    font-size: 18px;
}

.sidebar_top > h4{
    color: gray;
    font-size: 12px;
}
.sidebar_stats{
    padding: 10px;
    margin-bottom: 10px;
    border: 1px solid lightgray;
    background-color: white;
    border-radius: 0 0 10px 10px;
}
.sidebar_stat{
    margin-top: 10px;
    display: flex;
    justify-content: space-between;
}
.sidebar_stat > p{
    color: gray;
    font-size: 13px;
    font-weight: 600;
}
.sidebar_statNumber{
    font-weight: bold;
    color: #0a66c2 !important;
}
.sidebar_bottom{
    text-align: left;
    padding: 10px;
    border: 1px solid lightgray;
    background-color: white;
    border-radius: 10px;
    margin-top: 10px;
}
.sidebar_recentItems{
     display: flex;
     font-size: 13px;
     color: gray;
     font-weight: bolder;
     cursor: pointer;
     margin-bottom: 5px;
     padding: 5px;
}
.sidebar_recentItems:hover{
    background-color: whitesmoke;
    border-radius: 10px;
    color: black;
    cursor: pointer;
}
.sidebar_bottom > p{
    font-size: 13px;
    padding-bottom: 10px;
}